  outline-offset: 2px;
}
.invert {
  --tw-invert: invert(100%);
  filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
}
.filter {
  filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
}
.\[background-image\:repeating-linear-gradient\(to_bottom\2c _transparent\2c _transparent_23px\2c _\#FDE68A_23px\2c _\#FDE68A_24px\)\] {
  background-image: repeating-linear-gradient(to bottom, transparent, transparent 23px, #FDE68A 23px, #FDE68A 24px);
}
/* @import "tailwindcss"; */

/* 사용자 스타일 */
:root {
  --background: #f9fafb;
  --foreground: #171717;
}

body {
  background-color: var(--background);
  color: var(--foreground);
  display: flex;
  flex-direction: column;
}

.placeholder\:text-slate-400::-moz-placeholder {
  --tw-text-opacity: 1;
  color: rgb(148 163 184 / var(--tw-text-opacity, 1));
}

.placeholder\:text-slate-400::placeholder {
  --tw-text-opacity: 1;
  color: rgb(148 163 184 / var(--tw-text-opacity, 1));
}

@media (min-width: 640px) {
  .sm\:inline {
    display: inline;
  }
  .sm\:w-full {
    width: 100%;
  }
  .sm\:w-\[120px\] {
    width: 120px;
  }
  .sm\:w-\[100px\] {
    width: 100px;
  }
  .sm\:w-\[151px\] {
    width: 151px;
  }
  .sm\:flex-none {
    flex: none;
  }
  .sm\:flex-row {
    flex-direction: row;
  }
  .sm\:flex-col {
    flex-direction: column;
  }
}

@media (min-width: 768px) {
  .md\:w-1\/2 {
    width: 50%;
  }
  .md\:w-full {
    width: 100%;
  }
  .md\:flex-1 {
    flex: 1 1 0%;
  }
  .md\:flex-\[2\] {
    flex: 2;
  }
  .md\:flex-row {
    flex-direction: row;
  }
}
